<template>
  <Card :bordered="false" dis-hover>
    <Form ref="searchDiv" :label-width="80"></Form>
    <Row>
      <Col span="24" class-name="layout-table-tools" ref="toolsBar">
        <Row type="flex" justify="space-between">
          <Col span="6"
            ><Button
              type="primary"
              style="margin-right: 5px"
              @click="edit('add')"
            >
              <Icon type="md-add-circle" /> 新增
            </Button></Col
          >
          <!-- <Col span="6" style="text-align:right"
            ><Button type="primary" style="margin-right: 5px">
              展开所有子栏目
            </Button></Col
          > -->
        </Row>
      </Col>

      <Col span="24" class-name="layout-table-list">
        <Table
          row-key="name"
          :loading="loading"
          :size="tableOpts.size"
          :height="tableOpts.height"
          @on-selection-change="selectChange"
          ref="selection"
          :columns="tableCol"
          :data="tableData"
        >
          <template slot-scope="{ row }" slot="title">
            <strong>{{ row.title }}</strong>
          </template>
        </Table>
        <Button @click="handleSelectAll()" style="margin-right: 5px"
          >批量操作</Button
        >
        <Button @click="batchdetele">删除</Button>
      </Col>
    </Row>
    <Modal v-model="addModal" :title="modalTitle" loading @on-cancel="cancel">
      <Form :model="addForm" ref="addFrom" :label-width="80" :rules="addRlue">
        <FormItem label="名称" prop="name">
          <Input v-model="addForm.name" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="描述" prop="description">
          <Input v-model="addForm.description" placeholder="请输入"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="cancel">取消</Button>
        <Button type="primary" :loading="modal_loading" @click="save('addFrom')"
          >保存</Button
        >
      </div>
    </Modal>
  </Card>
</template>
<script>
import {
  appParentModuleList,
  appParentModuleNew,
  appParentModuleUpdate
} from '@/api/menu.js'
import { SERVER_CODE } from '@/utils/constants.js'
import { spinShowMixins } from '@/mixins/spin-mixins.js'
import { menu } from '@/utils/common.js'
export default {
  mixins: [spinShowMixins],
  data() {
    return {
      selectList: [],
      loading: false, //表格数据加载
      tableCol: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'title',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h(
              'Input',
              {
                props: {
                  type: 'text',

                  size: 'small',
                  value: params.index + 1
                },
                style: {
                  align: 'center'
                },
                on: {
                  'on-blur': event => {
                    console.log(event.target.value)
                    console.log(params)
                  }
                }
              },
              ''
            )
          }
        },
        {
          title: '栏目名称',
          key: 'name',
          tree: true
        },
        {
          title: '所属模块',
          slot: 'title',
          width: 300,
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 250,
          fixed: 'right',
          align: 'center',
          render: (h, params) => {
            if (params.row.type != 'menu') {
              return h('div', [
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.addChildren(params)
                      }
                    }
                  },
                  '添加子栏目'
                ),
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.edit('edit', params)
                      }
                    }
                  },
                  '编辑'
                ),
                h(
                  'Button',
                  {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.remove(params)
                      }
                    }
                  },
                  '删除'
                )
              ])
            } else {
              return h('div', [
                h(
                  'Button',
                  {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.edit('edit', params)
                      }
                    }
                  },
                  '编辑'
                ),
                h(
                  'Button',
                  {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.remove(params)
                      }
                    }
                  },
                  '删除'
                )
              ])
            }
          }
        }
      ],
      tableData: [],
      addRlue: {
        name: [
          {
            required: true,
            message: '请输入',
            trigger: 'blur'
          }
        ],
        description: [
          {
            required: true,
            message: '请输入',
            trigger: 'blur'
          }
        ]
      },
      addModal: false,
      addForm: {
        id: '',
        name: '',
        description: ''
      },
      modalTitle: '',
      modalFlag: '',
      modal_loading: false
    }
  },
  created() {},
  activated() {
    if (this.$store.state.menuStatus) {
      this.getData() //页面是否重新加载数据
      this.$store.state.menuStatus = false
    }
  },
  methods: {
    getData() {
      this.loading = true
      let params = {}
      appParentModuleList(params)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.tableData = menu(data) //数据处理加上一级二级三级标识
            this.loading = false
            this.selectList = [] //清空单选框选择数据
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSelectAll() {
      //单选框全选与取消
      let self = this
      self.selectall = !self.selectall
      self.$refs.selection.selectAll(self.selectall)
    },
    batchdetele() {
      //批量删除
      if (this.selectList.length == 0) {
        this.$Message.info('请选择删除对象！')
      } else {
        this.tableData = []
        this.selectList = [] //清空单选框选择数据
      }
    },
    selectChange(res) {
      //选择框状态变化监听
      this.selectList = res
    },
    addChildren(param) {
      //跳转增加菜单页面
      sessionStorage.setItem('menuInfo', JSON.stringify(param))
      switch (param.row.type) {
        case 'parent':
          //增加二级菜单
          this.$router.push({ name: 'menuAddModule', params: { type: 'add' } })
          break
        case 'module':
          //增加三级菜单
          this.$router.push({ name: 'menuAddMenu', params: { type: 'add' } })
          break
        default:
          this.$Message.info('不能再添加子菜单!')
          break
      }
    },
    edit(type, param) {
      //type=add 为parent菜单增加 type=edit时为编辑
      //注：type=edit时为编辑时 param.row.type=parent 一级 默认弹出框  module 二级跳转修改 menu三级跳转修改
      this.modalFlag = type

      if (type == 'add') {
        this.modalTitle = '增加菜单'
        this.$refs.addFrom.resetFields()
        this.addModal = true
      } else {
        sessionStorage.setItem('menuInfo', JSON.stringify(param))
        switch (param.row.type) {
          case 'parent':
            this.modalTitle = '修改菜单'
            this.$refs.addFrom.resetFields()
            this.addForm.id = this.tableData[param.index].id
            this.addForm.name = this.tableData[param.index].name
            this.addForm.description = this.tableData[param.index].description
            this.addModal = true
            break
          case 'module':
            this.$router.push({
              name: 'menuAddModule',
              params: { type: 'edit' }
            })
            break
          default:
            this.$router.push({ name: 'menuAddMenu', params: { type: 'edit' } })
            break
        }
      }
    },
    save(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.handlerAddParent()
        }
      })
    },
    /**
     * 处理菜单
     */
    async handlerAddParent() {
      switch (this.modalFlag) {
        case 'add':
          await appParentModuleNew(this.addForm).then(({ code }) => {
            if (code === SERVER_CODE.CODE_200) {
              this.$Message.success('添加菜单成功!')
            }
          })
          break
        case 'edit':
          await appParentModuleUpdate(this.addForm).then(({ code }) => {
            if (code === SERVER_CODE.CODE_200) {
              this.$Message.success('菜单编辑成功!')
            }
          })
          break
      }
      setTimeout(() => {
        this.addModal = false
      }, 2000)
    },
    cancel() {
      this.addModal = false
    },
    remove(index) {
      //删除一条数据
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>该操作将导致数据无法恢复，请谨慎操作！</p>',
        onOk: () => {
          console.log(index)
        },
        onCancel: () => {}
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.layout-table-tools {
  button {
    margin-right: 12px;
  }
}
</style>
